.App-container {
  max-width: 960px;
  min-height: 100vh;
  background-color: #f3f4f6;
  margin: auto; 
}
div.custom-container {
  width: 100%;
  height: 70vh;
  text-align: center;
}

svg.custom .node image {
  fill:white;
  background-color: white;
  margin:30px;
}

/* svg.custom .node rect {
  fill: red;
  stroke: black;
  stroke-width: 5;
  opacity: 0.5;
} */

svg.custom .node text {
  display:block;
  font-size: 14px;
  background-color: #444;
  fill: #f4f4f4;
  text-shadow: 0 1px 4px black;
  transform:translate(40px, 6%) rotate(-90deg);
  
}

svg.custom .node {
  cursor: pointer;
}

svg.custom path.link {
  fill: none;
  stroke: #2593b8;
  stroke-width: 1.5px;
}